.input {
display: flex;
align-items: center;
margin-bottom: 0.5rem ;
}

.input label {
  font-weight: bold;
  margin-right: 1rem;
}

.input input {
  width: 3rem;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: inherit;
  padding-left: 0.5rem;
}
