.form {
  text-align: right;
}

.form button {
  cursor: pointer;
  background: #8a2b06;
  color: white;
  font-size: 1rem;
  border-radius: 20px;
  padding: 0.25rem 2rem;
  border: 1px solid #8a2b06;
  font-weight: bold;
}

.form button:hover,
.form button:active {
  background-color: #641e03;
  border-color: #641e03;
}  
